 "),_c('p',{staticClass:"p51 p18 fontColor"},[_c('span',[_vm._v("1、我们不会与中经联盟以外的任何公司、组织和个人共享您的个人信息，但以下情况除外：")])]),_vm._v(" "),_c('p',{staticClass:"p52 p18 fontColor"},[_vm._v("\n        （1）事先获得您明确的同意或授权；"),_c('br'),_vm._v("\n        （2）根据适用的法律法规、法律程序的要求、强制性的行政或司法要求所必须的情况下进行提供；"),_c('br'),_vm._v("\n        （3）在法律法规允许的范围内，为维护中经联盟、中经联盟的关联方或合作伙伴、您或其他中经联盟用户或社会公众利益、财产或安全免遭损害而有必要提供；"),_c('br'),_vm._v("\n        （4） 只有共享您的信息，才能实现我们的产品与/或服务的核心功能或提供您需要的服务；"),_c('br'),_vm._v("\n        （5）应您需求为您处理您与他人的纠纷或争议；"),_c('br'),_vm._v("\n        （6）符合与您签署的相关协议（包括在线签署的电子协议以及相应的平台规则）或其他的法律文件约定所提供；"),_c('br'),_vm._v("\n        （7）基于学术研究而使用；"),_c('br'),_vm._v("\n        （8）基于符合法律法规的社会公共利益而使用；"),_c('br')]),_vm._v(" "),_c('p',{staticClass:"p53 p18 fontColor"},[_c('span',[_vm._v("2、我们可能会将您的个人信息与我们的关联方共享。但我们只会共享必要的个人信息，且受本隐私政策中所声明目的的约束。我们的关联方如要改变个人信息的处理目的，将再次征求您的授权同意。")])]),_vm._v(" "),_c('p',{staticClass:"p54 p18 fontColor"},[_c('span',[_vm._v("3、我们可能会向合作伙伴等第三方共享您的订单信息、账户信息、设备信息以及位置信息，以保障为您提供的服务顺利完成。但我们仅会出于合法、正当、必要、特定、明确的目的共享您的个人信息，并且只会共享提供服务所必要的个人信息。我们的合作伙伴无权将共享的个人信息用于任何其他用途。 我们的合作伙伴包括以下类型：")])]),_vm._v(" "),_c('p',{staticClass:"p55 p18 fontColor"},[_vm._v("\n        （1）商品或技术服务的供应商。"),_c('span',[_vm._v("我们可能会将您的个人信息共享给支持我们功能的第三方。")]),_vm._v("这些支持包括为我们的提供授权房源、供货或提供基础设施技术服务、物流配送服务、支付服务、数据处理等。我们共享这些信息的目的是可以实现我们的核心功能，比如我们必须与物流服务提供商共享您的订单信息才能安排送货；或者我们需要将您的订单号和订单金额与第三方支付机构共享以实现其确认您的支付指令并完成支付等。"),_c('span',[_vm._v("如您需开通中经联盟钱包功能、合同信息服务功能，我们将向第三方支付机构理房通及金融服务机构共享您的个人身份信息，以便您使用理房通的金融功能。 ")])]),_vm._v(" "),_c('p',{staticClass:"p56 p18 fontColor"},[_vm._v("\n        （2）第三方商家。我们需要将您的位置信息与房源经纪公司或房源管理公司共享以为您提供更适合的房源购置或租住。"),_c('span',[_vm._v("我们必须将您的订单信息与交易有关的必要信息与第三方商家共享来实现您向其购买商品或服务的需求，并促使其可以完成后续的售后服务。")])]),_vm._v(" "),_c('p',{staticClass:"p57 p18 fontColor"},[_vm._v("\n        （3）委托我们进行推广的合作伙伴。有时我们会代表其他企业向使用我们产品与/或服务的用户群提供促销推广的服务。"),_c('span',[_vm._v("我们可能会使用您的个人信息以及您的非个人信息集合形成的间接用户画像与委托我们进行推广的合作伙伴（“委托方”）共享，但我们仅会向这些委托方提供推广的覆盖面和有效性的信息，而不会提供您的个人身份信息，或者我们将这些信息进行汇总，以便它不会识别您个人。")]),_vm._v("比如我们可以告知该委托方有多少人看了