       6、互联网环境并非百分之百安全，我们将尽力确保您发送给我们的任何信息的安全性。如果我们的物理、技术、或管理防护设施遭到破坏，导致信息被非授权访问、公开披露、篡改、或毁坏，导致您的合法权益受损，我们将承担相应的法律责任。\n      ")]),_vm._v(" "),_c('p',{staticClass:"p84 p18 fontColor"},[_vm._v("\n        7、安全事件处置\n      ")]),_vm._v(" "),_c('p',{staticClass:"p85 p18 fontColor"},[_vm._v("\n        在通过中经联盟与第三方进行网上商品或服务的交易时，您不可避免的要向交易对方或潜在的交易对方披露自己的个人信息，如联络方式或者邮政地址等。请您妥善保护自己的个人信息，仅在必要的情形下向他人提供。\n      ")]),_vm._v(" "),_c('p',{staticClass:"p85 p18 fontColor"},[_vm._v("\n        为应对个人信息泄露、损毁和丢失等可能出现的风险，中经联盟制定了多项制度，明确安全事件、安全漏洞的分类分级标准及相应的处理流程。中经联盟也为安全事件建立了专门的应急响应团队，按照安全事件处置规范要求，针对不同安全事件启动安全预案，进行止损、分析、定位、制定补救措施、联合相关部门进行溯源和打击。\n      ")]),_vm._v(" "),_c('p',{staticClass:"p86 p18 fontColor"},[_vm._v("\n        在不幸发生个人信息安全事件后，我们将按照法律法规的要求，及时向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。我们同时将及时将事件相关情况以邮件、信函、电话、推送通知等方式告知您，难以逐一告知个人信息主体时，我们会采取合理、有效的方式发布公告。同时，我们还将按照监管部门要求，主动上报个人信息安全事件的处置情况。\n      ")]),_vm._v(" "),_c('p',{staticClass:"p87 p18 fontColor"},[_vm._v("\n        8、如果您对我们的个人信息保护有任何疑问，可通过本政策最下方约定的联系方式联系我们。"),_c('span',[_vm._v("如您发现自己的个人信息泄密，尤其是您的账户及密码发生泄露，请您立即通过本政策最下方约定的联系方式联络我们，以便我们采取相应措施。")])]),_vm._v(" "),_c('p',{staticClass:"p88 p60 fontColor"},[_c('span',[_vm._v("（二）您个人信息的保存")])]),_vm._v(" "),_c('p',{staticClass:"p89 p18 fontColor"},[_vm._v("\n        1、您的个人信息将全被存储于中华人民共和国境内。如您使用跨境交易服务，且需要向境外传输您的个人信息完成交易的，我们会单独征得您的授权同意并要求接收方按照我们的说明、本隐私政策以及其他任何相关的保密和安全措施来处理这些个人信息。\n      ")]),_vm._v(" "),_c('p',{staticClass:"p90 p18 fontColor"},[_vm._v("\n        2、除非法律法规另有规定，您的个人信息我们将保存至您账号注销之日后的一个月。我们承诺这是为了保证您在中经联盟的消费者权益，您个人信息在中经联盟须保存的最短期间。当您的个人信息超出我们所保存的期限后，我们会对您的个人信息进行删除或匿名化处理。\n      ")]),_vm._v(" "),_c('p',{staticClass:"p91 p18 fontColor"},[_c('span',[_vm._v("3、请您注意，当您成功申请注销中经联盟账户后，我们给予您自注销之日起一个月的“后悔期”，在这个期间内您可以随时申请恢复您已经注销的中经联盟账户。为了实现您恢复您中经联盟账户的功能，我们将不会在“后悔期”内对您的个人信息进行删除或匿名化处理。如您在“后悔期”内没有要求恢复您的中经联盟账户或您明确向我们表述不再恢复您的中经联盟账户，我们将对您的个人信息进行删除或匿名化处理。")])]),_vm._v(" "),_c('p',{staticClass:"p92 p18 fontColor"},[_c('span',[_vm._v("4、如果我们终止服务或运营，我们会至少提前三十日向您通知，并在终止服务或运营后对您的个人信息进行删